// –Ü–º–ø–æ—Ä—Ç—É—î–º–æ –±—ñ–±–ª—ñ–æ—Ç–µ–∫—É iziToast –¥–ª—è –≤—ñ–¥–æ–±—Ä–∞–∂–µ–Ω–Ω—è –ø–æ–≤—ñ–¥–æ–º–ª–µ–Ω—å
import iziToast from 'izitoast';
import 'izitoast/dist/css/iziToast.min.css';

// –Ü–º–ø–æ—Ä—Ç—É—î–º–æ —Ñ—É–Ω–∫—Ü—ñ—é –¥–ª—è –æ—Ç—Ä–∏–º–∞–Ω–Ω—è –∑–æ–±—Ä–∞–∂–µ–Ω—å –∑ Pixabay API
import { getImagesByQuery } from './js/pixabay-api';

// –Ü–º–ø–æ—Ä—Ç—É—î–º–æ –¥–æ–ø–æ–º—ñ–∂–Ω—ñ —Ñ—É–Ω–∫—Ü—ñ—ó –¥–ª—è —Ä–æ–±–æ—Ç–∏ –∑ –≥–∞–ª–µ—Ä–µ—î—é —Ç–∞ –∑–∞–≤–∞–Ω—Ç–∞–∂—É–≤–∞—á–µ–º
import { createGallery, clearGallery, showLoader, hideLoader } from './js/render-functions';

// –ó–Ω–∞—Ö–æ–¥–∏–º–æ —Ñ–æ—Ä–º—É –ø–æ—à—É–∫—É –Ω–∞ —Å—Ç–æ—Ä—ñ–Ω—Ü—ñ
const form = document.querySelector('.form');

// –î–æ–¥–∞—î–º–æ —Å–ª—É—Ö–∞—á–∞ –ø–æ–¥—ñ—ó submit –¥–æ —Ñ–æ—Ä–º–∏
form.addEventListener('submit', handleSubmit);

// –û–±—Ä–æ–±–Ω–∏–∫ –ø–æ–¥—ñ—ó –Ω–∞–¥—Å–∏–ª–∞–Ω–Ω—è —Ñ–æ—Ä–º–∏
async function handleSubmit(event) {
  event.preventDefault(); // –°–∫–∞—Å–æ–≤—É—î–º–æ —Å—Ç–∞–Ω–¥–∞—Ä—Ç–Ω—É –ø–æ–≤–µ–¥—ñ–Ω–∫—É (–ø–µ—Ä–µ–∑–∞–≤–∞–Ω—Ç–∞–∂–µ–Ω–Ω—è —Å—Ç–æ—Ä—ñ–Ω–∫–∏)

  // –û—Ç—Ä–∏–º—É—î–º–æ –∑–Ω–∞—á–µ–Ω–Ω—è –∑ –ø–æ–ª—è –ø–æ—à—É–∫—É, –æ–±—Ä—ñ–∑–∞—é—á–∏ –ø—Ä–æ–±—ñ–ª–∏
  const searchQuery = event.target.elements['search-text'].value.trim();

  // –Ø–∫—â–æ –ø–æ–ª–µ –ø–æ—à—É–∫—É –ø–æ—Ä–æ–∂–Ω—î ‚Äî –ø–æ–∫–∞–∑—É—î–º–æ –ø–æ–ø–µ—Ä–µ–¥–∂–µ–Ω–Ω—è —ñ –∑—É–ø–∏–Ω—è—î–º–æ –≤–∏–∫–æ–Ω–∞–Ω–Ω—è
  if (!searchQuery) {
    iziToast.warning({
      title: 'Warning',
      message: 'Please enter a search term',
      position: 'topRight',
    });
    return;
  }

  // –ü–æ–∫–∞–∑—É—î–º–æ –∑–∞–≤–∞–Ω—Ç–∞–∂—É–≤–∞—á —Ç–∞ –æ—á–∏—â—É—î–º–æ –ø–æ–ø–µ—Ä–µ–¥–Ω—é –≥–∞–ª–µ—Ä–µ—é
  showLoader();
  clearGallery();

  try {
    // –û—Ç—Ä–∏–º—É—î–º–æ –¥–∞–Ω—ñ –∑–æ–±—Ä–∞–∂–µ–Ω—å –∑–∞ –ø–æ—à—É–∫–æ–≤–∏–º –∑–∞–ø–∏—Ç–æ–º
    const data = await getImagesByQuery(searchQuery);

    // –Ø–∫—â–æ —Ä–µ–∑—É–ª—å—Ç–∞—Ç—ñ–≤ –Ω–µ–º–∞—î ‚Äî –ø–æ–≤—ñ–¥–æ–º–ª—è—î–º–æ –∫–æ—Ä–∏—Å—Ç—É–≤–∞—á–∞
    if (data.hits.length === 0) {
      iziToast.error({
        title: 'Error',
        message: 'Sorry, there are no images matching your search query. Please try again!',
        position: 'topRight',
      });
      return;
    }

    // –°—Ç–≤–æ—Ä—é—î–º–æ –≥–∞–ª–µ—Ä–µ—é –∑ –æ—Ç—Ä–∏–º–∞–Ω–∏—Ö –∑–æ–±—Ä–∞–∂–µ–Ω—å
    createGallery(data.hits);
  } catch (error) {
    // –û–±—Ä–æ–±–∫–∞ –ø–æ–º–∏–ª–æ–∫ –∑–∞–ø–∏—Ç—É ‚Äî –ø–æ–≤—ñ–¥–æ–º–ª—è—î–º–æ –∫–æ—Ä–∏—Å—Ç—É–≤–∞—á–∞ —ñ –≤–∏–≤–æ–¥–∏–º–æ –ø–æ–º–∏–ª–∫—É –≤ –∫–æ–Ω—Å–æ–ª—å
    iziToast.error({
      title: 'Error',
      message: 'Something went wrong. Please try again later.',
      position: 'topRight',
    });
    console.error(error);
  } finally {
    // –•–æ–≤–∞—î–º–æ –∑–∞–≤–∞–Ω—Ç–∞–∂—É–≤–∞—á —Ç–∞ —Å–∫–∏–¥–∞—î–º–æ —Ñ–æ—Ä–º—É
    hideLoader();
    form.reset();
  }
}
